export interface ZIndex {
  spinner: number;
  tooltip: number;
  notification: number;
  hint: number;
  modal: number;
  dropdown: number;
}

const MAX_INT = 100;
export const zIndex: ZIndex = {
  spinner: MAX_INT,
  tooltip: MAX_INT,
  notification: MAX_INT - 1,
  hint: MAX_INT - 2,
  modal: MAX_INT - 3,
  dropdown: MAX_INT - 4,
};
